.reviews {
  padding: 0 @mobile-padding;
  padding-top: 40px;
  padding-bottom: 40px;

  @media (min-width: @tablet-width) {
    padding-top: 64px;
    padding-bottom: 17px;
  }

  @media (min-width: @desktop-width) {
    padding: 0 @desktop-padding;
    padding-top: 186px;
    padding-bottom: 32px;

    background-image: url("../../img/quotes.png");
    background-size: 79px 63px;
    background-position: top 72px center;
    background-repeat: no-repeat;
  }
}

.reviews__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.reviews__list {
  .list ();
  margin-bottom: 34px;

  @media (min-width: @tablet-width) {
    margin-bottom: 38px;
  }

  @media (min-width: @desktop-width) {
    padding: 0 120px;
    margin-bottom: 0;
  }
}

.reviews__blockquote {
  display: flex;
  flex-direction: column;
  align-items: center;

  margin: 0;
}

.reviews__auhtor {
  margin-bottom: 13px;

  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  text-align: center;

  @media (min-width: @tablet-width) {
    order: 2;

    margin-bottom: 0;

    font-size: 20px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 3px;
    font-size: 24px;
  }
}

.reviews__auhtor-details {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
    order: 3;

    font-size: 14px;
    line-height: 30px;
  }

  @media (min-width: @desktop-width) {
    font-size: 18px;
  }
}

.reviews__text {
  margin: 0;

  font-size: 14px;
  line-height: 30px;
  text-align: center;

  @media (min-width: @tablet-width) {
    font-size: 18px;

    margin-bottom: 36px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 35px;
  }
}

.reviews__controls {
  display: none;

  @media (min-width: @desktop-width) {
    position: absolute;
    top: 28px;
    content: "";

    display: flex;
    justify-content: space-between;

    width: 100%;
  }
}

.reviews__control {

}

.reviews__toggles {
  max-width: 256px;

  @media (min-width: @desktop-width) {
    display: none;
  }
}
